import React from "react";
import {useState} from "react"
import {BrowserRouter as Router, Link, Route} from "react-router-dom";
import PrivateRoute from './PrivateRoute';
import Home from "./pages/Home";
import Admin from "./pages/Admin";
import Login from "./pages/Login";
import Signup from "./pages/Signup";
import UserList from "./pages/userList";
import Closest from "./pages/Closest";
import {AuthContext} from "./context/auth";
import {UserContext} from "./context/user";
import 'bootstrap/dist/css/bootstrap.min.css';
import "./assets/css/dashboard.css";
import videoView from "./pages/videoView";
import Profile from "./pages/Profile";
import Side from "./components/Sidebar";
import Graphs from "./pages/Graphs";

//

function App(props) {
    // set auth token
    const [authTokens, setAuthTokens] = useState(localStorage.getItem('tokens') || '');

    const setTokens = (data) => {
        localStorage.setItem("tokens", data.token);
        setAuthTokens(data);
    }
    console.log(localStorage.getItem("tokens"))

    //set user
    const [username, setUsername] = useState(localStorage.getItem('username') || '');

    const setName = (data) => {
        localStorage.setItem("username", data);
        setUsername(data);
    }
    console.log(localStorage.getItem("username"))

    return (
        <AuthContext.Provider value={{authTokens, setAuthTokens: setTokens}}>
            <UserContext.Provider value={{username, setUsername: setName}}>
                <Router>
                    <head>
                        <link rel="stylesheet" href="https://unpkg.com/leaflet@1.6.0/dist/leaflet.css"
                              integrity="sha512-xwE/Az9zrjBIphAcBb3F6JVqxf46+CDLwfLMHloNu6KEQCAWi6HcDUbeOfBIptF7tcCzusKFjFw2yuvEpDL9wQ=="
                              crossOrigin=""/>
                        <script src="https://unpkg.com/leaflet@1.6.0/dist/leaflet.js"
                                integrity="sha512-gZwIG9x3wUXg2hdXF6+rVkLF/0Vi9U8D2Ntg4Ga5I5BZpVkVxlJWbSQtXPSiUTtC0TjtGOmxa1AJPuV0CPthew=="
                                crossOrigin=""></script>

                        <script src="https://cdn.jsdelivr.net/npm/chart.js@2.8.0"></script>

                    </head>
                    <div>

                    </div>

                    <div className="wrapper">

                                                        <Side/>




                            <div id="maincontent">
<Route exact path={'/'} component={Home}/>
                                <Route exact path={'/CSIPatientDashboard/'} component={Home}/>
                                <Route exact path={"/CSIPatientDashboard/login"} component={Login}/>
                                <Route exact path={"/CSIPatientDashboard/signup"} component={Signup}/>
                                <PrivateRoute path={"/CSIPatientDashboard/admin"} component={Profile}/>
                                <PrivateRoute path={"/CSIPatientDashboard/interventions"} component={videoView}/>
                                <PrivateRoute path={"/CSIPatientDashboard/profile"} component={Profile}/>
                                <PrivateRoute path={"/CSIPatientDashboard/graphs"} component={Graphs}/>
                            </div>
                    </div>
                </Router>
            </UserContext.Provider>
        </AuthContext.Provider>
    )
        ;
}

export default App;