import React, {useState} from "react";
import {Menu, MenuItem, ProSidebar} from "react-pro-sidebar";
import {Link} from "react-router-dom";
import {AuthContext} from "../context/auth";

function Home(props) {
     const [authTokens, setAuthTokens] = useState(localStorage.getItem('tokens') || '');

    const setTokens = (data) => {
        localStorage.setItem("tokens", data.token);
        setAuthTokens(data);
    }
    console.log(localStorage.getItem("tokens"))
  return <AuthContext.Provider value={{authTokens, setAuthTokens: setTokens}} ><div><h3 style={{textAlign: "center", marginTop: "20px"}}>Welcome to CSI Patient Dashboard Demo!</h3>

   {authTokens === '' ?
                    <h5 style={{textAlign: "center", marginTop: "20px"}}>Login with the username and password given to you to check it out</h5>

                    :
                    <h5 style={{textAlign: "center", marginTop: "20px"}}>You are logged in, enjoy this demo!</h5>
                }

  </div></AuthContext.Provider>;
}

export default Home;